import { h, Component } from 'preact';
import { Form } from '../components/form';
import { settings } from '../lib/settings';

const ipBlockLevel = [
    { name: ' ', value: 0 },
    { name: '  ', value: 1 },
    { name: ' IP ', value: 2 },
]

const formConfig = {
    groups: {
        general: {
            name: '',
            configs: {
                unitname: { name: ' ', type: 'string' },
                unitnr: { name: ' ', type: 'number' },
                appendunit: { name: '    ', type: 'checkbox' },
                password: { name: ' ', type: 'password', var: 'security[0].password' },
            }
        },
        wifi: {
            name: 'WiFi',
            configs: {
                ssid: { name: 'SSID', type: 'string', var: 'security[0].WifiSSID' },
                passwd: { name: '', type: 'password', var: 'security[0].WifiKey' },
                fallbackssid: { name: ' SSID', type: 'string', var: 'security[0].WifiSSID2' },
                fallbackpasswd: { name: ' ', type: 'password', var: 'security[0].WifiKey2' },
                wpaapmode: { name: 'WPA AP Mode Key:', type: 'string', var: 'security[0].WifiAPKey' },
            }
        },
        clientIP: {
            name: ' IP ',
            configs: {
                blocklevel: { name: ' IP ', type: 'select', options: ipBlockLevel, var: 'security[0].IPblockLevel' },
                lowerrange: { name: ' IP  ', type: 'ip', var: 'security[0].AllowedIPrangeLow' },
                upperrange: { name: ' IP  ', type: 'ip', var: 'security[0].AllowedIPrangeHigh' },
            }
        },
        IP: {
            name: ' IP',
            configs: {
                ip: { name: 'IP ', type: 'ip' },
                gw: { name: '', type: 'ip' },
                subnet: { name: '', type: 'ip' },
                dns: { name: 'DNS', type: 'ip' },
            }
        },
        sleep: {
            name: ' ()',
            configs: {
                awaketime: { name: '    ', type: 'number' },
                sleeptime: { name: ' ', type: 'number' },
                sleeponfailiure: { name: '   ', type: 'checkbox' },
            }
        }
    },
}

export class ConfigPage extends Component {
    render(props) {
        formConfig.onSave = (values) => {
            settings.set(`config`, values);
            window.location.href='#devices';
        }
        const config = settings.get('config');
        return (
            <Form config={formConfig} selected={config} />
        );
    }
}
